import React from 'react';

const Card = (props) => {
  
    
    return (
        <div>
        <div style={{maxWidth: "300px"}} className="card">
        <img className="cardImg" src={props.image} alt=""  />
        <div className="cardBody">
        <p className="title" >{props.d}</p>
        <a href={props.link} style={{textDecoration: "underline"}} target="_blank">Live site</a> <br/> <br/>
        <a style={{ color: "#24262e",cursor: "pointer", textDecoration: "none"}} href={props.git} target="_blank" >  <i style={{ fontSize: "25px"}} className="icon-social-github" ></i> {" "} GitHub repo</a>
        </div>

 
    </div>
        </div>
    );
};


export default Card;
