import React, { Component } from 'react';
import wix from "../img/wix.png";
import telkom from "../img/telkom.png";
import orange from "../img/orange.png";
import email from "../img/maail.png";
import app from "../img/app.png";
import blog from "../img/blog.png";
import Card from './Card';

class Port extends Component {
   

    render() {
        return (
            <div className="port" >
                <div className="wrap">
                    <h1> My Portfolio</h1>
                    <hr style={{ border: "2px solid #24262e"}} className="line"/>
                    <div className="cards">
                      <Card d="Built a lookalike of Wix's website for my portfolio. I hand coded this site from scratch, using technologies like HTML, sass( compiled to CSS),Bootstrap, Jquery" git="https://github.com/makhosi6/Wix-_clone" image={wix} link={"https://makhosi6.github.io/Wix-_clone/"} />
                       <Card d="Used a StartBootstrap template here and i modified it a bit" git="https://github.com/makhosi6/orange" image={orange} link="https://makhosi6.github.io/orange/" />
                       <Card d="Built a nice Responsive-HTML-Email, with in-line CSS &. HTML." git="https://github.com/makhosi6/Responsive-HTML-Email" image={email} link="https://makhosi6.github.io/Responsive-HTML-Email/"/>
                       <Card d="Built a lookalike of Telkom's website for my portfolio. I hand coded this site from scratch, using technologies like HTML, sass( compiled to CSS),Bootstrap, Javascript. " git="https://github.com/makhosi6/telkomdotcom-clone" image={telkom} link={"https://makhosi6.github.io/telkomdotcom-clone/"} />
                       <Card d="Developed a React application that gets data from a third party API and it also has a search functionality " git="https://github.com/makhosi6/movie-app" image={app} link={"https://makhosi6.github.io/movie-app/"}/>
                       <Card  d="Developed a blogger's website template that has a dark-mode option for the readers."  git="https://github.com/makhosi6/blog-in-dark-mode" image={blog} link={"https://makhosi6.github.io/blog-in-dark-mode/"} />

                    </div>
                </div>
                
 
            </div>
        );
    }
}

export default Port;